import * as ActionType from './home-constant';

const initialState = {

}

const HomeReducer = (state={...initialState},action) => {
    switch(action.type){
        case ActionType.NAV_TO_HOME:
            return{
                ...state,
                data:action.data
            }
        default:
            return state;
    }

};
export default HomeReducer;


